import React from "react";
import { graphql } from "gatsby"
import { GatsbyImage, getImage } from "gatsby-plugin-image"

import Layout from "../components/layouts/layout"
import Container from "../components/container"
import Section from "../components/section"
import Seo from "../components/seo"
import ResultCombo from "../components/teams/result_combo"

const IndexPage = ({data}) => {
  const { allInstagramContent } = data // data.mdx holds your post data
  const instas = allInstagramContent.edges
  return (
    <Layout title={"Home"}>
      <Section>
        <Container>
          <div className="columns">
            <div className="column is-12 content">
              <h2>Spielbetrieb</h2>
              <ResultCombo shv_id={140428} verein={true}/>
            </div>
          </div>
        </Container>
      </Section>

      <Section>
        <Container>
          <div className="columns is-multiline">
            <div className="column is-12 content">
              <h2>News</h2>
            </div>
              {instas.map(({ node }) => {
                const title = node.caption
                switch (node.media_type) {
                  case 'IMAGE':
                    return (
                        <>
                          <div className="card column is-3 m-2">
                            <div className="card-image">
                              <a href={node.permalink}>
                                <GatsbyImage
                                    image={getImage(node.localFile)}
                                    alt=""
                                />
                              </a>
                            </div>
                            <div className="card-content">
                              <div className="content">{title}</div>
                            </div>
                          </div>
                        </>
                    )
                  case 'VIDEO':
                    return (
                        <>
                          <div className="card column is-3 m-2">
                            <h4>Video</h4>
                          </div>
                        </>
                    )
                  case 'CAROUSEL_ALBUM':
                    return (
                        <>
                          <div className="card column is-3 m-2">
                            <div className="card-image">
                              <a href={node.permalink}>
                                <GatsbyImage
                                    image={getImage(node.localFile)}
                                    alt=""
                                />
                              </a>
                            </div>
                            <div className="card-content">
                              <div className="content">{title}</div>
                            </div>
                          </div>
                        </>
                    )
                  default:
                    return (
                        <>
                          <div className="card column is-3 m-2">
                            <h4>unknown type {node.media_type}</h4>
                          </div>
                        </>
                    )
                }
              })}
          </div>
        </Container>
      </Section>
    </Layout>
  )
}

export const Head = () => <Seo title="TV Zofingen Handball" />

export default IndexPage

export const frontNewsQuery = graphql`
  query frontNewsQuery {
    allInstagramContent(limit: 3) {
      edges {
        node {
          caption
          media_url
          media_type
          permalink
          localFile {
            childImageSharp {
              gatsbyImageData(layout: CONSTRAINED, placeholder: BLURRED)
            }
          }
          album {
            localFile {
              childImageSharp {
                gatsbyImageData(layout: CONSTRAINED, placeholder: BLURRED)
              }
            }
          }
        }
      }
    }
  }
`
